import React, { useRef ,useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import TodoComponent from '../components/Todos/index'
import {fetchTodo}  from '../redux/actions/index';

const TodoConainer = () => {
  const dispatch = useDispatch();
  
  useEffect(() => {
    dispatch(fetchTodo());
  }, []);

  const actionData = useSelector(state => state.todo.result, []) || [];

  console.log(actionData);
  return (
    <div>
      <h4>https://jsonplaceholder.typicode.com/todos 연습중..</h4>
       <TodoComponent actionData={actionData} />
    </div>
  )
};
export default TodoConainer;