import React, {} from 'react';

const HistoryList = ({data, active, currentRef, onMouseMove}) => {

  return (
    <li className={`${active ? "active" : ""}`} ref={active ? currentRef : null}>
      <strong>{data.year}</strong>
      <span className="type">{data.type}</span>
      <div className="textArea">
        {data.work.map((cont, i) => (
          <dl key={i}>
            <dt>{cont.subj}</dt>
            <dd>
              <ul>
                {cont.text.map((list, i) => (
                  <li key={i}>{list}</li>
                ))}
              </ul>
            </dd>
          </dl>
        ))}
      </div>
    </li>
  )
};
export default HistoryList;