import axios from 'axios';

export const FETCH_WORK = 'FETCH_WORK';
export const FETCH_HISTORY = 'FETCH_HISTORY';
export const FETCH_TODO = 'FETCH_TODO';

export const workData = (data) => {
    return {
        type: FETCH_WORK,
        data
    }
}

export const fetchWork = () => {
  return (dispatch) => {
      return axios.get(`./work.json`)
      .then(response => {
          dispatch(workData(response.data))
      })
      .catch(error => {
          throw(error);
      });
  }
}

export const historyData = (data) => {
    return {
        type: FETCH_HISTORY,
        data
    }
}

export const fetchHistory = () => {
  return (dispatch) => {
      return axios.get(`./history.json`)
      .then(response => {
          dispatch(historyData(response.data))
      })
      .catch(error => {
          throw(error);
      });
  }
}


export const todoData = (data) => {
    return {
        type: FETCH_TODO,
        data
    }
}

export const fetchTodo = () => {
  return (dispatch) => {
      return axios.get(`https://jsonplaceholder.typicode.com/todos`)
      .then(response => {
          dispatch(todoData(response.data))
      })
      .catch(error => {
          throw(error);
      });
  }
}
