import React, { useRef ,useState, useEffect } from 'react';
import { motion, useTransform } from "framer-motion";
import ReactScrollWheelHandler from "react-scroll-wheel-handler"; //stricMode dom property warring
import { useSelector, useDispatch } from 'react-redux';
import ListComponent from '../components/Work/list';
import {fetchWork}  from '../redux/actions/index';

const Work = ({headerSub, x, y}) => {
  const dispatch = useDispatch();
  
  let rotateX = useTransform(y, [0, 1000], [25, -25])
  let rotateY = useTransform(x, [0, 1920], [-30, 30])
  let boxX = useTransform(y, [0, 1920], [40, -40])
  let boxY = useTransform(x, [0, 1920], [100, -100])
  let thumX = useTransform(y, [0, 1920], [45, -45])
  let thumY = useTransform(x, [0, 1920], [-45, 45])
  let tX = useTransform(y, [0, 1000], [65, -65])
  let tY = useTransform(x, [0, 1920], [-65, 65])

  const obj = {thumX, thumY, tX, tY};

  const listData = useSelector(state => state.work.result, []) || [];
  const workList = useRef(null);
  const currentRef = useRef(null);
  const [activeNum, setActiveNum] = useState(0);
  const doPrev = Math.max(0, activeNum - 1);
  const doNext = Math.min(listData.length - 1, activeNum + 1);

  const prev = () => {
    setActiveNum(doPrev);
    workList.current.style.transform = 'translateX(-' + (currentRef.current.offsetWidth) * doPrev +'px)';
    //console.log('prev', activeNum);
  };

  const next = () => {
    setActiveNum(doNext);
    workList.current.style.transform = 'translateX(-' + (currentRef.current.offsetWidth) * doNext +'px)';
    //console.log('next', activeNum);
  };

  const reset = () => {
    setActiveNum(0);

    console.log(workList);
    workList.current.style.transform = 'translateX(0)';
  }

  useEffect(() => {
    headerSub ();
    reset();
    dispatch(fetchWork());
  }, [headerSub, ListComponent]);

  const pageVariants = {
    initial: {
      opacity: 0,
      y: '200px',
      scale: 0.8
    },
    in: {
      opacity: 1,
      y: 0,
      scale: 1
    },
    out: {
      opacity: 0,
      y: '300px',
      scale: 0.8
    },
  }
  const pageTransition = {
    //ease: "easeInOut",
    delay: 0.3,
    type: "spring", 
    stiffness: 80,
    transition: {
      //delayChildren: 0.8,
    }
  };

  return (
    <motion.div
      className={"workCircle"}
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={pageTransition}
    >
      <ReactScrollWheelHandler
        upHandler={prev}
        downHandler={next}
        leftHandler={next}
        rightHandler={prev}
        wheelConfig={[9, 100, 0.02]}
        customStyle={{
          width: "100%",
          height: "calc(100vh - 135px)",
        }}
      >
        <motion.div
          style={{
            height:"100%",
            rotateX: rotateX,
            rotateY: rotateY,
        }}
        >
          <div className="list">
            <h3>Work<i></i></h3>
            <p className="info">리뉴얼 및 서비스종료 등의 프로젝트는 제외 되었습니다.</p>

            <ListComponent data={listData} ulRef={workList} currentRef={currentRef} activeNum={activeNum} obj={obj}  />

            <span className="circle"></span>
            <motion.div
                className={"circleBack"}
                style={{
                  translateX: boxY,
                  //translateY: boxY,
              }}
            />
          </div>
        </motion.div>
      </ReactScrollWheelHandler>
      <motion.div
          className={"box"}
          style={{
            // height:"100%",
            // rotateX: boxX,
            // rotateY: boxY,
        }}
      />
    </motion.div>
  )
};
export default Work;