import React, { useState, useRef, useEffect } from 'react';
import {Route, Switch, Redirect , useHistory, useLocation } from 'react-router-dom';
import { motion, AnimatePresence, useMotionValue } from "framer-motion";
import Header from "./components/header";
import Main from "./container/main";
import Work from "./container/work";
import History from "./container/history"
import Todo from "./container/todos";

const App = () => {
  const location = useLocation();
  const [isMainNav, setMainNav] = useState(false);
  const [isMain, setMain] = useState(true);
  const [isHistory, setHistory] = useState(false);
  const video = useRef(null);
	const wave = useRef(null);
  let isPlaying = false;

  const headerSub = () => {
    setMain(false);
    setMainNav(true);
  }
  const mainState = () => {
    setMain(true);
    video.current.load();
    videoPlay();
  }
  const historyInit = () =>{
    setHistory(true);
  }
  
	const videoPlay = (e) => {
		if(isPlaying){
			return;
    }
    setMainNav(false);
    //video.current.play();
    
    let play = video.current.play();

    if (play !== undefined) {
      play
        .then(_ => {
          isPlaying = true;
          
          video.current.addEventListener('timeupdate', (e) => {
            if(!wave.current){
              return;
            }
            let currentTime = e.target.currentTime;
            if(e.target.ended){
              isPlaying = false;
              //e.target.load();
            }
            //console.log(currentTime);
            if(currentTime > 2){
              setMainNav(true);
              return;
            }
            wave.current.style.transform = 'translateY(-' + (currentTime * 50) +'px)';
            //console.log(e.target.currentTime);
          });
        })
        .catch(error => {
        });
    }
  }

  const history = useHistory();

  const x = useMotionValue(100)
  const y = useMotionValue(100)

  const handleMouse = (event) => {
    x.set(event.pageX)
    y.set(event.pageY)
    //console.log(event.pageX, event.pageY)
  }

  useEffect(() => {
    
    return history.listen((location) => {
      let timer;
      if(location.pathname === '/'){
        setMainNav(false);
        setHistory(false);

      } else if(location.pathname === '/history') {
        timer = setTimeout(()=>{
          setHistory(true);
        },800);
      } else {
        clearTimeout(timer);
        setMainNav(true);
        setHistory(false);
      }
    })
  },[history])

	return (
    <motion.div
      className={"rootWrap"}
      style={{
        perspective: 3600,
        height:"100%"
      }}
      onMouseMove={handleMouse}
    >
      <Header 
        isMainNav={isMainNav} 
        isMain={isMain} 
        headerSub={headerSub}
        isHistory={isHistory}
      /> 
      <AnimatePresence>
        <Switch location={location} key={location.pathname}>
          <Route exact path="/" render={() => 
            <Main 
              isMainNav={isMainNav} 
              videoEl={video}
              videoPlay={videoPlay} 
              // videoStop={videoStop}
              mainState={mainState}
              isMain={isMain} 
              wave={wave} 
              />} 
            />
          <Route exact path="/work" render={() => <Work headerSub={headerSub} x={x} y={y} />} />
          <Route exact path="/history" render={() => <History headerSub={headerSub} historyInit={historyInit} isHistory={isHistory} />} />
          <Route exact path="/todo" render={() => <Todo />} />
          <Redirect path="*" to="/" />
        </Switch>
      </AnimatePresence>
    </motion.div>
	);
}

export default App;