import React, { useRef ,useState, useEffect } from 'react';
import { motion } from "framer-motion";
import ReactScrollWheelHandler from "react-scroll-wheel-handler"; //stricMode dom property warring
import { useSelector, useDispatch } from 'react-redux';
import List from '../components/History/list';
import {fetchHistory}  from '../redux/actions/index';

const History = ({headerSub, historyInit, isHistory}) => {
  const dispatch = useDispatch();

  const historyList = useRef(null);
  const currentRef = useRef(null);
  const bar = useRef(null);
  const [historyNum, setHistoryNum] = useState(0);

  const historyData = useSelector(state => state.history.result, []) || [];
  const doUp = Math.max(0, historyNum - 1);
  const doDown = Math.min(historyData.length - 1, historyNum + 1);

  const prev = () => {
    setHistoryNum(doUp);
    let pos = (currentRef.current.offsetHeight) * doUp;
    historyList.current.style.transform = 'translateY(-' + pos +'px)';
    progress(pos);
    //console.log(doUp);
  };

  const next = () => {
    setHistoryNum(doDown);
    let pos = (currentRef.current.offsetHeight) * doDown;
    historyList.current.style.transform = 'translateY(-' + pos +'px)';
    progress(pos);
  };

  const reset = () => {
    setHistoryNum(0);
    historyList.current.style.transform = 'translateY(0)';
  }

  const pageVariants = {
    initial: {
      opacity: 0,
    },
    in: {
      opacity: 1,
    },
    out: {
      opacity: 0,
    },
  }
  const pageTransition = {
    type: "tween",
    ease: "linear",
    delay: 0.3,
    //duration: 0.5,
    transition: {
      delayChildren: 0.4,
    }
  };

  const progress = (s) => {
    //100 / height * measure
    console.log(s);
    let pos = 100 / (historyList.current.offsetHeight - 442) * s;
    bar.current.style.width= ((pos < 100) ? pos : '100')  + "%";
    //console.log(historyList.current.offsetHeight, windowH);
  }


  useEffect(() => {
    headerSub ();
    setTimeout(()=>{
      historyInit ();
    },500);
    reset();
    dispatch(fetchHistory());

  }, [headerSub, historyInit, historyList]);


  return (
    <motion.div
      className={"history"}
      initial="initial"
      animate="in"
      exit="out"
      variants={pageVariants}
      transition={pageTransition}
    >
      <ReactScrollWheelHandler
        upHandler={prev}
        downHandler={next}
        wheelConfig={[9, 100, 0.02]}
        customStyle={{
          width: "100%",
          height: "100%",
        }}
      >
        <span className="progress"><span ref={bar}></span></span>
        <div className={"list" + (isHistory ? ' show' : '')}>
          <ul ref={historyList}>
            {historyData.map((data, idx) => {
              const active = idx === historyNum;
              return (
                <List 
                  key={data.id}
                  id={data.id}
                  data={data}
                  active={active} 
                  currentRef={currentRef} 
                />
              )
            })}
          </ul>
          <span className="circle"></span>
        </div>
      </ReactScrollWheelHandler>
    </motion.div>
  )
};
export default History;